.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.heart {
  color: #ff0000;
}

.small {
  font-size: 0.75rem;
}
.con{
  margin: auto;
  width:40%;
  height:100vh;
  border: 1px solid;
  margin-top: 10%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  background-color: white;
}
#b{
  background-color:blue;
  margin-left: 0px;
  border-radius: 4px;
  color: white;
  border: 1px solid blue;
  width:60px;
  height:25px;
}
#in{
  width:70%;
  height: 25px;
  border-radius: 6px;
  border:1px solid white;
  background-color: rgba(128,128,128,0.1);
}
#h{
  color:rgba(0, 0, 0, 0.8);
}
body{
  background-color: rgba(128,128,128,0.1);
}
.l{
  margin-right: 10%;
}